.github_box {
   width: 100%;
   padding: 1rem;
   background: linear-gradient(#222, gray);


   .github_mini_box {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 5px;
   }

   & h1 {
      text-align: center;
      font-size: 2.4rem;
      text-decoration: underline;
      margin: 2rem 0;
      color: #f0f0f0;
   }

   .github_user_card {
      width: 33%;
      border: 1px solid rebeccapurple;
      display: flex;
      background: #f0f0f0;

      .github_user_img {
         width: 50%;
         display: flex;

         & img {
            width: 100%;
            height: auto;
            object-fit: cover;
         }
      }

      .github_user_detalis {
         margin: 0 1.5rem;

         & h2 {
            font-size: 1.8rem;
            color: #222;
            font-weight: 400;
            margin-bottom: 1rem;
         }

         & p {
            font-size: 1.6rem;
            color: purple;
            font-weight: 300;
            text-align: center;
            margin-bottom: 1rem;
         }

         .user_handle_detalis {
            display: flex;
            gap: 10px;
            background-color: gray;

            .user_handle {
               display: flex;
               flex-direction: column;
               align-items: center;

               .item_name {
                  font-size: 1.6rem;
                  color: rebeccapurple;
                  font-weight: 200;
                  padding: 0.5rem;
               }

               .item_number {
                  font-size: 1.6rem;
                  color: #222;
                  font-weight: 300;
                  padding: 0.5rem;
               }
            }
         }
      }
   }
}